body {
  margin: 0;
}

.App {
  text-align: center;
}


h1 {
  font-family: 'Bigelow Rules';
  font-size:72px;
  color: #0ccac4;
}


.contacts {
  display: flex;
  flex-wrap: wrap;
}

.contact-card {
  flex-basis: 250px;
  margin: 20px;
}

.contact-card > img {
  width: 100%;
  height: auto;
}

.contact-card > h3 {
  text-align: center;
}

.contact-card > p {
  font-size: 12px;
}